import React, { FC, useState } from 'react'
import Sultan from '../../public/images/Sultan.png'
import Arrow from '../../public/images/Arrow.png'
import Download from '../../public/images/Download.png'
import WhatsApp from '../../public/images/WhatsApp.png'
import Telegram from '../../public/images/Telegram.png'
import Visa from '../../public/images/Visa.png'
import Mastercard from '../../public/images/Mastercard.png'
import '../styles/footer.scss'

interface ILink {
    description: string
}

const menuItems: ILink[] = [
    {
        description: ' ',
    },
    {
        description: '  ',
    },
    {
        description: '',
    },
    {
        description: '',
    },
]

const categoryItems: ILink[] = [
    {
        description: ' ',
    },
    {
        description: '  ',
    },
    {
        description: '  ',
    },
    {
        description: '    ',
    },
    {
        description: '',
    },
]

const Footer: FC = () => {
    return (
        <div className="footer">
            <div className="footer__content">
                <img className="footer__sultan" src={Sultan} alt=""></img>
                <div className="footer__describe">
                           " "  
                      
                </div>
                <div className="footer__promotions">
                    <div className="promotions__describe">    </div>
                    <div className="promotions__input">
                        <input placeholder="  E-mail"></input>
                        <div className="input__send">
                            <img src={Arrow} alt=""></img>
                        </div>
                    </div>
                </div>
            </div>

            <div className="footer__content">
                <div className="footer__links">
                    <div className="links__title"> :</div>
                    {menuItems.map((item, index) => (
                        <div key={index} className="links__item">
                            {item.description}
                        </div>
                    ))}
                </div>
            </div>

            <div className="footer__content">
                <div className="footer__links">
                    <div className="links__title">:</div>
                    {categoryItems.map((item, index) => (
                        <div key={index} className="links__item">
                            {item.description}
                        </div>
                    ))}
                </div>
            </div>

            <div className="footer__content">
                <div className="footer__price-list">
                    <div className="price-list__title"> -:</div>
                    <div className="price-list__bottom">
                        <div className="bottom__description">-</div>
                        <img src={Download} alt="" />
                    </div>
                </div>
                <div className="footer__social-network">
                    <div className="social-network__title">  </div>
                    <div className="social-network__links">
                        <img src={WhatsApp} alt="" />
                        <img src={Telegram} alt="" />
                    </div>
                </div>
            </div>

            <div className="footer__content">
                <div className="footer__contacts">
                    <div className="contacts__title">:</div>
                    <div className="contacts__phone">+7 (777) 490-00-91</div>
                    <div className="contacts__schedule"> : 9:00-20:00</div>
                    <div className="contacts__call"> </div>
                    <div className="contacts__email">
                        <div className="email__content">opt.sultan@mail.ru</div>
                        <div className="email__schedule">    </div>
                    </div>
                </div>

                <div className="footer__payment">
                    <img src={Visa} alt="" />
                    <img src={Mastercard} alt="" />
                </div>
            </div>
        </div>
    )
}

export default Footer
